@import url('https://fonts.googleapis.com/css?family=Lato:400,700|Inconsolata');
@import '~prismjs/themes/prism.css';

html, body, #root, #navigation {
  height: 100%;
  min-height: 100%;
  max-width: 100vw;
}

body {
  margin: 0;
  padding: 0;
  font-family: Lato, sans-serif;
  font-size: 16px;
  line-height: 1.5rem;
}

code[class*="language-"], pre[class*="language-"] {
  font-size: 0.8rem;
}


